.home-banner {
    padding-top: 120px;
    @media #{$vpdesktop} {
        padding-top: 110px;
    }
}
.b-thumb-wrapper {
    position: relative;
    .b-thumb-img {
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            @media #{$vpmobile} {
                height: 550px;
            }
        }
    }
    .b-thumb-content {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50% , -50%);
        @media #{$vptabletP} {
            width: 100%;
            padding: 0 20px;
            max-width: 700px;
        }
        .h1 {
            text-align: center;
            margin-bottom: 15px;
        }
        p { 
            text-align: center;
        }
    }
}
.quick-links {
    padding: 40px 0 0;
    .quick-link-bar {
        display: flex;
        .js-img-slide {
            @media #{$vptabletP} {
                justify-content: center;
            }
        }
        @media #{$vptabletP} {
            flex-wrap: wrap;
            justify-content: center;
        }
        .link-content {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            flex: 0 0 17%;
            max-width: 17%;
            transition: all 0.35s ease;
            position: relative;
            @media #{$vptabletP} {
                flex: 0 0 100%;
                max-width: 100%;
                margin-bottom: 20px;
            }
            @media #{$vpssmobile} {
                flex: 0 0 100%;
                max-width: 100%;
            }
            @media #{$vpsmmobile} {
                flex: 0 0 100%;
                max-width: 100%;
            }
            .sec-img {
                width: 60px;
                min-height: 60px;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 0 auto;
                transition: all 0.35s ease;
                .tosvg {
                    width: 53px;
                    path,rect {
                        transition: all 0.35s ease;
                    }
                }
            }
            p {
                margin: 20px 0 5px;
                text-align: center;
            }
            a {
                color: $red;
                font-size: 14px;
                font-weight: 500;
                justify-content: center;
                display: flex;
            }
            &.sty1 {
                flex: 0 0 15%;
                max-width: 15%; 
                transition: none;
                transform: scale(1);
                @media #{$vptabletP} {
                    transform: none;
                    width: 0 !important;
                    display: none !important;
                    width: 0 !important;
                }
                }
                .sep {
                    border-left: 1px solid $blue-dark;
                    height: 100%;
            }
            &:hover {
                .sec-img {
                    transform: scale(1.05);
                    .tosvg {
                      path,rect {
                          fill: $red;
                          stroke: $red;
                      }  
                    }
                } 
            }
            &:last-child::after {
                content: "";
                position: absolute;
                width: 1px;
                height: 125px;
                background: $black;
                right: 144%;
                @media #{$vptabletP} {
                    display: none;
                }
            }
            &:last-child {
                margin-left: auto;
            }
        } 
        ul {
            &.slick-dots {
                display: flex;
                flex: 0 0 70px;
                max-width: 70px;
                justify-content: space-between; 
                li {
                   button {
                    background: transparent;
                    border-radius: 50px;
                    width: 5px;
                    height: 5px;
                    text-indent: -9999px;
                    padding: 3px;
                    border: 1px solid $red;
                   }
                   &.slick-active {
                       button {
                           background: $red;
                           &:focus {
                               outline: none;
                           }
                       }
                   } 
                }
            }
        }
    }
}