html {
    direction: ltr;
    width: 100%;
    &.browserIos {
        overflow-x: hidden;
    }
}

body {
    width: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    font-size: 18px;
    line-height: 1.3;
    font-family: $primaryFont;
    @media #{$vpdesktop} {
        font-size: 15px;
    }
    @media #{$vptabletP} {
        font-size: 13px;
    }
}

::-moz-selection {
    background: yellowgreen;
    text-shadow: none;
}

::selection {
    background: yellowgreen;
    text-shadow: none;
}

hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid #ccc;
    margin: 1em 0;
    padding: 0;
}

*,
*:after,
*:before {
    outline: none;
    box-sizing: border-box;
}

img {
    max-width: 100%;
}

a {
    text-decoration: none !important;
    outline: none !important;
    &:hover {
        text-decoration: none !important;
    }
}

p {
    margin: 10px 0;
    i {
        font-style: italic;
    }
}

svg {
    transform: translateZ(0);
}

audio,
canvas,
iframe,
img,
svg,
video {
    vertical-align: middle;
}

textarea {
    resize: none;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

.o-hidden {
    overflow: hidden;
    height: 100%;
}

.u-pos-s {
    position: static;
}

.u-pos-r {
