            max-width: 1600px;
        }
    }
}
.btn {
    font-size: 18px;
    font-weight: 600;
    padding: 12px 55px 12px 40px;
    border-radius: 25px;
    text-transform: uppercase;
    text-align: left;
    transition: all 0.35s ease 0s;
    @media #{$vpdesktop} {
        font-size: 15px;
    }
    @media #{$vptabletP} {
        font-size: 13px;
    }
    &.btn-primary {
        position: relative;
        background-color: transparent;
        border-color: $blue-dark;
        color: $blue-dark;
        border-radius: 0;

        &:not(:disabled):not(.disabled).active:focus,
        &:not(:disabled):not(.disabled):active:focus,
        &.focus,
        &:focus {
            box-shadow: 0 0 2px 0.2rem rgba($blue-dark, 0.3);
        }

        &:not(:disabled):not(.disabled).active,
        &:not(:disabled):not(.disabled):active,
        &:hover,
        &.focus,
        &:focus {
            background-color: $blue-dark;
            border-color: $blue-dark;
            color: $white;
            .tosvg {
                transform: translate(5px,-50%);
                path {
                    fill: $white;
                }
            }
        }
        .tosvg {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            margin-left: 15px; 
            transition: all 0.35s ease;
            right: 35px;
        }
    }
    &.btn-white {
        position: relative;
        background-color: transparent;
        border-color: $white;
        color: $white;
        border-radius: 0;

        &:not(:disabled):not(.disabled).active:focus,
        &:not(:disabled):not(.disabled):active:focus,
        &.focus,
        &:focus {
            box-shadow: 0 0 2px 0.2rem rgba($white, 0.3);
        }

        &:not(:disabled):not(.disabled).active,
        &:not(:disabled):not(.disabled):active,
        &:hover,
        &.focus,
        &:focus {
            background-color: $white;
            border-color: $white;
            color: $white;
        }
        .tosvg {
            position: absolute;
            top: 50%;
            margin-left: 15px;
            transform: translateY(-50%);
            right: 35px;
        }
    }
    &.btn-link {
        position: relative;
        padding: 0;
        border: none;
        display: flex;
        max-width: 120px;
        border-radius: 0;
        align-items: center;
        border-color: none;
        color: $black-light;
        text-transform: initial;
        background-color: transparent;
        border-bottom: 2px solid $black-light;
        transform: translateX(0);
        .tosvg {
            margin-right: 10px;
        }

        &:not(:disabled):not(.disabled).active:focus,
        &:not(:disabled):not(.disabled):active:focus,
        &.focus,
        &:focus {
            box-shadow: none;
        }

        &:not(:disabled):not(.disabled).active,
        &:not(:disabled):not(.disabled):active,
        &:hover,
        &.focus,
        &:focus {
            background-color: transparent;
            color: $red;
            border-color: $red;
            transform: translateX(10px);
        }
        @media #{$vptabletP} {
            max-width: 95px;
        }
    }
}
.sec-padded {
    padding: 45px 0;

    &--bottom {
        padding-bottom: 45px;
    }

    &--top {
        padding-top: 45px;
    }
    &--head {
        padding-top: 213px;
        padding-bottom: 45px;
        @media #{$vpdesktop} {
            padding-top: 150px;
        }
    }
    &--head2 {
        padding-top: 213px;
        padding-bottom: 45px;
        @media #{$vpdesktop} {
            padding-top: 200px;
        }
    }
    @media #{$vptabletL} {
        padding: 30px 0;

        &--bottom {
            padding-bottom: 30px;
        }

        &--top {
            padding-top: 30px;
        }
        &--head {
            padding-top: 198px;
            padding-bottom: 30px;
        }
        &--head2 {
            padding-top: 200px;
            padding-bottom: 30px;
        }
    }

    @media #{$vpmobile} {
        padding: 25px 0;

        &--bottom {
            padding-bottom: 25px;
        }

        &--top {
            padding-top: 25px;
        }
        &--head {
            padding-top: 120px;
            padding-bottom: 25px;
        }
        &--head2 {
            padding-top: 200px;
            padding-bottom: 25px;
        }
    }
    
}
.desktop-view {
    display: block;
    @media #{$vptabletP} {
        display: none !important;
    }
}
.mobile-view {
    display: none;
    @media #{$vptabletP} {
