::selection {
    background: rgba($blue-dark, 1);
    text-shadow: none;
    color: $white;
}

@-webkit-keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

@-moz-keyframes spin {
    0% {
        -moz-transform: rotate(0deg);
    }
    100% {
        -moz-transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
.js-loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1003;
    background: rgba($blue-dark, 1);
    display: block;
    > div {
        display: block;
        position: relative;
        left: 50%;
        top: 50%;
        width: 150px;
        height: 150px;
        margin: -75px 0 0 -75px;
        border-radius: 50%;
        border: 3px solid transparent;
        border-top-color: $white;
        transform: translate3d(0, 0, 0);
        animation: spin 2s linear infinite;
        &::before {
            content: "";
            position: absolute;
            top: 5px;
            left: 5px;
            right: 5px;
            bottom: 5px;
            border-radius: 50%;
            border: 3px solid transparent;
            border-top-color: $red;
            -webkit-animation: spin 3s linear infinite;
            animation: spin 3s linear infinite;
        }
        &::after {
            content: "";
            position: absolute;
            top: 15px;
            left: 15px;
            right: 15px;
            bottom: 15px;
            border-radius: 50%;
            border: 3px solid transparent;
            border-top-color: $white;
            animation: spin 1.5s linear infinite;
        }
    }
}
.c-side-nav {
    position: relative;
    margin: 26px 0;
    &.js-fixed-sidebar {
        position: absolute;
        width: 390px;
        
        &.is--fixed {
            position: fixed;
            top: 267px;
        }
    
        &.is--bottom {
            position: absolute;
            bottom: 0;
            top: auto;
        }
    }
}
.select-box {
    @media #{$vptabletP} {
        margin-bottom: 0 !important;
    }
}

.tabs-fix-wrap {
    width: 100%;
    position: relative;
    background-color: #FFFFFF;
    z-index: 99;
    &.fix-this {
        position: fixed;
    }
}