.sec-corporate-agb {
    .post-agb-wrapper {
        .post-agb-content {
            text-align: center;
            max-width: 76.45%;
            margin: 0 auto;
            @media #{$vpmobile} {
                max-width: 100%;
            }
            .item-txt-blue {
                span {
                    color: $blue-dark;
                    font-weight: bold;
                    line-height: 30px;
                    text-transform: uppercase;
                }
            }
            .style-content {
                margin-bottom: 45px;
                @media #{$vptabletP}  {
                    margin-bottom: 25px;
                }
                .h2 {
                    margin: 13px 0 7px 0;
                }
            }
        }
        .post-agb-img {
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}
.sec-agb-details {
    background-color: $light-grey;
    .agb-details-wrapper {
        max-width: 76.44%;
        text-align: center;
        margin: 0 auto;
        @media #{$vpmobile} {
            max-width: 100%;
        }
        .agb-details-content {
            padding: 50px 0 70px;
            @media #{$vpdesktop}  {
                padding: 30px 0 50px;
            }
            .h2 {
                padding-bottom: 20px;
                margin: 0;
                @media #{$vpmobile}  {
                    font-size: 30px;
                    line-height: 38px;
                    padding-bottom: 0;
                }
                @media #{$vpsmmobile}  {
                    font-size: 20px;
                    line-height: 28px;
                    padding-bottom: 0;
                }
            }
            p {
                margin: 20px 0 0 0;
            }
        }
    }
}
.sec-cards {
    h2 {
        text-align: center;
    }
    .sec-cards-wrapper {
        display: flex;
        justify-content: space-between;
        @media #{$vptabletP}  {
            flex-wrap: wrap;
        }
        .post-agb-card {
            max-width: 32%;
            flex: 0 0 32%;
            background-color: $light-grey;
            @media #{$vptabletP}  {
                max-width: 49%;
                flex: 0 0 49%;
                margin-bottom: 14px;
            }
            @media #{$vpmobile}  {
                max-width: 100%;
                flex: 0 0 100%;
                margin-bottom: 14px;
            }
            .card-img {
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            .card-content {
                padding: 35px;
                @media #{$vpdesktop} {
                    padding: 20px;
                }
                .h2 {
                    text-align: left;
                    @media #{$vptabletL}  {
                        font-size: 25px;
                        line-height: 40px;
                    }
                }
                p {
                    color: $black-light;
                    font-weight: 400;
                }
            }
        }
    }
}
.sec-content-box {
    background-color: $light-grey;
    
    .content-box {
        background-color: $white;
        .content-box-wrapper {
            display: flex;
            @media #{$vptabletP}  {
                flex-wrap: wrap;
            }
            .content-box-content {
                max-width: 60%;
                flex: 0 0 60%;
                padding: 45px 60px;
                @media #{$vptabletP}  {
                    max-width: 100%;
                    flex: 0 0 100%;
                }
                p {
                    padding: 10px 0 20px 0;
                }
            }
            .content-box-img {
                max-width: 40%;
                flex: 0 0 40%;
                @media #{$vptabletP}  {
                    max-width: 100%;
                    flex: 0 0 100%;
                }
                img {
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
}