.sec-content-box {
    background-color: $light-grey;
    
    .content-box {
        background-color: $white;
        .content-box-wrapper {
            display: flex;
            @media #{$vptabletP}  {
                flex-wrap: wrap;
                flex-direction: column-reverse;
            }
            &.sty1 {
                flex-direction: row-reverse;
                @media #{$vptabletP} {
                    flex-direction: column-reverse;
                }
            }
            .content-box-content {
                max-width: 60%;
                flex: 0 0 60%;
                padding: 45px 60px;
                @media #{$vpdesktop} {
                    padding: 30px;
                }
                @media #{$vptabletP} {
                    max-width: 100%;
                    flex: 0 0 100%;
                }
                p {
                     padding: 10px 0 20px 0;
                }
            }
            .content-box-img {
                max-width: 40%;
                flex: 0 0 40%;
                overflow: hidden;
                position: relative;
                @media #{$vptabletP}  {
                    max-width: 100%;
                    flex: 0 0 100%;
                }
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    transition: all 0.35s ease;
                }
                &:hover {
                    img {
                        transform: scale(1.05);
                    }
                }
                &.sty1 {
                    overflow: visible;
                }
            }
        }
    }
    &.sec-content-box--corporate{
        padding: 0;
        margin-bottom: 180px;
        background-color: $blue-dark;
        @media #{$vptabletP}  {
            margin-bottom: 60px;
        }
        &.box--grey{
            background-color: $light-grey;
            .content-box{
                .content-box-wrapper{
                    .content-box-content{
                        .heading2{
                            color: $black-light;
                        }
                        p{
                            color: rgba($black-light, 0.8);
                        }
                        .btn{
                            &.btn-primary{
                                color: $blue-dark;
                                border-width: 2px;
                                border-color: $blue-dark;
                                &:hover{
                                    color: $white;
                                }
                            }
                        }
                    }
                }
            }
        }
        .content-box{
            background-color: transparent;
            .content-box-wrapper{
                .content-box-content{
                    padding-top: 95px;
                    @media #{$vptabletP}  {
                        padding-top: 20px;
                    }
                    .heading2{
                        color: $white;
                    }
                    p{
                        color: rgba($white, 0.8);
                    }
                    .btn{
                        &.btn-primary{
                            color: $white;
                            border-width: 2px;
                            border-color: $white;
                        }
                    }
                }
                .content-box-img{
                    height: auto;
                    @media #{$vptabletP}  {
                        height: auto;
                    }
                    img{
                        margin-top: 95px;
                        @media #{$vptabletP}  {
                            margin-top: 30px;
                        }
                    }
                }
            }
        }
    }   
}

.sec-corporate{
    padding-top: 45px;
}
.sec-contnet {
    max-width: 80%;
    @media #{$vpmobile} {
        max-width: 100%;
    }
    ul {
        li {
            padding: 10px;
        }
    }
    &.error-page {
        max-width: 100%;
        text-align: center;
        h4 {
            margin: 30px 0;
        }
        a {
            margin-top: 30px;
        }
    }
}