.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
}

.header-cover {
    background: $white;
    border-bottom: 1px solid #b7b7b7;

    .header-top {
        background: $blue-dark;
        padding: 5px 0;

        .sitetools {
            display: flex;
            justify-content: flex-end;

            .searchbox {
                width: 145px;
                position: relative;
                &:after {
                    content: "";
                    position: absolute;
                    top: 0;
                    width: 1px;
                    height: 33px;
                    right: -25px;
                    background-color: $white;
                    @media #{$vpdesktop} {
                        height: 22px;
                    }
                }

                .search {
                    width: 100%;
                    border: none;
                    color: $white;
                    font-size: 16px;
                    padding-right: 25px;
                    background: transparent;
                    border-bottom: 1px solid $white;
                    border-radius: 0;
                    @media #{$vpdesktop} {
                        font-size: 14px;
                    }
                    &::placeholder {
                        color: $white;
                    }
                }

                .search-icon {
                    background-color: transparent;
                    border: none;
                    position: absolute;
                    right: 0;
                    top: 0;

                    .tosvg {
                        position: absolute;
                        right: 0;
                        @media #{$vpdesktop} {
                            height: 15px;
                        }
                    }
                    &:focus {
                        outline: none;
                    }
                }
            }
        }
    }

    .sec-header {
        display: flex;
        justify-content: space-between;
        padding: 20px 0;
        align-items: center;
        .logobox {
            .tosvg {
                width: 260px;
                height: 40px;
                @media #{$vpdesktop} {
                    height: 35px;
                }
            }
        }

        .navigation {
            margin-right: -40px;
            .additional-icons {
                display: none;
            }
            ul {
                display: flex;
                @media #{$vptabletP} {
                    flex-direction: column;
                    background: #efefef;
                    width: 50%;
                    position: absolute;
                    height: 100vh;
                    top: 0;
                    padding-top: 140px;
                    right: 0;
                    z-index: 2;
                    &:after {
                        content: "";
                        position: absolute;
                        top: 70px;
                        left: 50%;
                        transform: translateX(-50%);
                        width: 100%;
                        border-top: 1px solid $gray-light;

                    }
                }
                @media #{$vpmobile} {
                    width: 80%;
                }

                li {
                    padding: 0 40px;
                    position: relative;
                    @media #{$vpMddesktop} {
                        padding: 0 20px;
                    }
                    @media #{$vptabletP} {
                        padding: 20px 40px;
                        &:before {
                            content: "";
                            position: absolute;
                            width: 80%;
                            height: 1px;
                            bottom: 0px;
                            left: 50%;
                            transform: translateX(-50%);
                            background-color: $blue-dark;
                            transition: all 0.35s ease;
                            @media #{$vpmobile} {
                                width: 90%;
                            }
                            @media #{$vpssmobile} {
                                width: 80%;
                            }
                        }
                    }

                    a {
                        color: $black;
                        font-weight: 500;
                        font-size: 20px;

                        @media #{$vpdesktop} {
                            font-size: 15px;
                        }
                        @media #{$vptabletL} {
                            font-size: 13px;
                        }
                        @media #{$vptabletP} {
                            font-size: 18px;
                            color: $blue-dark;
                        }
                        @media #{$vpmobile} {
                            font-size: 16px;
                        }
                        span {
                            display: block;
                            font-size: 14px;
                            font-weight: 400;
                            @media #{$vpdesktop} {
                                font-size: 12px;
                            }
                            @media #{$vptabletP} {
                                font-size: 14px;
                                color: $black-light;
                            }
                            @media #{$vpssmobile} {
                                font-size: 12px;
                            }
                        }
                        &:before {
                            content: "";
                            position: absolute;
                            width: 0;
                            height: 5px;
                            bottom: -21px;
                            left: 50%;
                            transform: translateX(-50%);
                            background-color: $blue-dark;
                            transition: all 0.35s ease;
    
                            @media #{$vptabletP} {
                                display: none;
                            }
                        }
                        .tosvg {
                            @media #{$vpmobile} {
                                right: -10px;
                            }
                        }
                    }

                    &:after {
                        content: "";
                        position: absolute;
                        width: 1px;
                        height: 100%;
                        border-left: 1px solid $gray-light;
                        top: 0;
                        right: 0;
                    }
                    @media #{$vptabletP} {
                        &:after {
                            content: "";
                            position: absolute;
                            background: url(../img/icons/ic-arrow-right-red.svg);
                            background-repeat: no-repeat;
                            width: 20px;
                            height: 20px;
                            top: 50%;
                            transform: translateY(-50%);
                            right: 50px;
                            border: none;
                        }
                    }
                    @media #{$vpmobile} {
                        &:after {
                            right: 30px;
                        }
                    }
                    &:last-child {
                        &:after {
                            display: none;
                        }
                        @media #{$vptabletP} {
                            &:after {
                                display: block;
                            }
                        }
                    }
                    &:hover,
                    &.active {
                        a {
                            &:before {
                                width: 80%;
                            }
                        }
                    }
                }
            }

            @media #{$vptabletP} {
                display: none;
                width: 100%;
                position: absolute;
                right: 0;
                top: 0;
                height: 100vh;
                background: rgba(0,0,0,0.8);
                padding: 100px;
                left: 0;
            }

            &.open {
                display: block;
                .additional-icons {
                    display: flex;
                    position: absolute;
                    right: 50px;
                    top: 25px;
                    z-index: 3;
                    width: 100%;
                    max-width: 44%;
                    flex: 0 0 44%;
                    justify-content: space-between;
                    @media #{$vpmobile} {
                        max-width: 71%;
                        flex: 0 0 71%;
                    }
                    @media #{$vpsmmobile} {
                        max-width: 65%;
                        flex: 0 0 65%;
                    }
                    .languageselector {
                        margin: 0;
                        .dropdown {
                            background: transparent;
                            .head {
                               span {
                                   &.text {
                                       color: $blue-dark;
                                       font-size: 18px;
                                   }
                                   &.arrow {
                                       &:after {
                                           border: none;
                                           background-image:url(../img/icons/arrow-up.svg) ;
                                           background-repeat: no-repeat;
                                           height: 10px;
                                           transform: rotate(180deg);
                                           top: 0;
                                       }
                                   }
                               } 
                            }
                            ul {
                                &.dropdownMenu {
                                    display: none;
                                    height: 100%;
                                    top: 20px;
                                    width: auto;
                                    padding: 0;
                                    right: 15px;
                                    li {
                                        background: transparent;
                                        padding: 10px 5px;
                                        a {
                                            color: $blue-dark;
                                            font-weight: 400;
                                            font-size: 18px;
                                        }
                                        &:after {
                                            display: none;
                                        }
                                    }
                                }
                                &:after {
                                    display: none;
                                }
                            }
                        }
                    } 
                    .header-icons {
                        display: flex;
                        padding-right: 30px;
                        @media #{$vpmobile} {
                            padding-right: 20px;
                        }
                        .icon-search {
                            margin-right: 30px; 
                            .tosvg {
                                width: 25px;
                                height: 25px;
                            }
                        }
                        .icon-location {
                            .tosvg {
                                width: 20px;
                                height: 25px;
                            }
                        }
                    }
                }
            }
        }
    }
}
.hamburger-menu {
    display: none;
    position: relative;
    cursor: pointer;
    -webkit-transition: opacity .25s ease;
    -o-transition: opacity .25s ease;
    transition: opacity .25s ease;
    margin: 0;
    width: 28px;
    height: 18px;
    z-index: 0;
    margin: 0 0 0 auto;
    padding: 3px 0 0 0;

    @media #{$vptabletP} {
        display: block;
    }
    &.active {
        position: absolute;
        right: 30px;
        top: 30px;
    }

    &:hover {
        opacity: .7;
        @media #{$vptabletP} {
            opacity: 1;
        }
    }

    span {
        position: absolute;
        top: 0;
        left: 0;
        height: 2.4px;
        width: 28px;
        border: none;
        display: block;
        cursor: pointer;
        border-radius: 5px;
        background: $black;
        transition: all .35s ease;
        border-radius: 5px;

        &:nth-of-type(2) {
            top: 6px;
        }

        &:nth-of-type(3) {
            top: 12px;
        }
    }
    &.active {
        z-index: 3;
        .top {
            transform: translateY(10px) translateX(0) rotate(45deg);
            top: -4px;
            left: 1px;
            background: $blue-dark;
        }

        .middle {
            opacity: 0;
        }

        .bottom {
            transform: translateY(-12px) translateX(0) rotate(-45deg);
            top: 18px;
            left: 0px;
            background: $blue-dark;
        }
    }
}

.languageselector {
    margin-left: 30px;
    position: relative;

    .dropdown {
        cursor: pointer;
        background-color: transparent;
        border: none;
        border-radius: 10px;
        padding: 4px 13px;
        display: block;
        font-size: 18px;
        line-height: 1;
        position: relative;
        background: $blue-dark;
        z-index: 1;

        .head {
            display: flex;
            align-items: center;

            .text {
                color: $white;
                font-size: 18px;
                line-height: 1;
                transition: all 0.35s ease;
                @media #{$vpdesktop} {
                    font-size: 16px;
                }
            }

            .arrow {
                margin-left: 8px;
                width: 10px;
                height: 10px;
                position: relative;
                transition: all 0.35s ease;

                &:after {
                    content: "";
                    position: absolute;
                    left: 0;
                    top: 4px;
                    width: 10px;
                    border: 5px solid transparent;
                    border-top-color: $white;
                }
            }
        }

        li {
            padding: 10px 0;

            a {
                color: $blue-dark;
                display: block;
            }
        }

        .dropdownMenu {
            display: none;
            width: 100%;
            position: absolute;
            background: #efefef;
            padding: 10px 20px;
            top: 31px;
        }
        &:hover {
            .head {
                .text {
                    color: $red;
                }
                .arrow {
                    &:after {
                        border-top-color: $red;
                    }
                }
            }
        }
    }
}

html[dir=ltr] .txt-en,
html[dir=rtl] .txt-ar {
    display: none;
}
.c-megaMenu-wrap {
    display: none;
    padding: 0!important;
}
.c-megaMenu-wrap .c-megaMenu-dropdown {
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    display: flex;
    overflow: hidden;
    margin-top: 0;
    border-radius: 0;
    padding: 50px !important;
    background: #efefef;
    box-shadow: 0 8px 10px 0 rgba(0,0,0,.35);
    @media #{$vptabletP} {
        width: 50%;
        flex-direction: column;
        height: 100vh;
        top: 0;
        right: 0;
        left: auto;
        z-index: 5;
        padding: 140px 0 0 0 !important;
    }
    @media #{$vpmobile} {
        width: 80%;
    }
}
.c-megaMenu-wrap {
    .c-megaMenu-dropdown {
        .c-megaMenu-left {
            width: 35%;
            height: 100%;
            background-color: #efefef;
            position: relative;
            @media #{$vptabletP} {
                width: 100%;
                height: auto;
                background-color: #efefef;
                position: relative;
            }
            .megamenu-content {
                max-width: 70%;
                padding-top: 25px;
                @media #{$vptabletP} {
                    max-width: 100%;
                }
                &.for-desktop {
                    .btn-link {
                        margin-top: 50px;
                    }
                }
                h2 {
                    margin: 0;
                }
                p {
                    margin: 60px 0 30px;
                }
                .btn-link {
                    max-width: 145px;
                    margin-bottom: 20px;
                    @media #{$vpdesktop} {
                        max-width: 125px;
                    }
                    .tosvg {
                        width: 13px;
                        height: 13px;
                        margin-left: 10px;
                        margin-right: 0;  
                        path {
                            stroke: $red;
                        }
                    } 
                }
                &.for-desktop {
                    @media #{$vptabletP} {
                        display: none;
                    }
                }
            }
        }
        .c-megaMenu-right {
            width: 65%;
            background: #efefef;
            border-radius: 0;
            height: auto;
            position: relative;
            @media #{$vptabletP} {
                width: 100%;
                overflow: scroll;
                max-height: 80%;
            }
            .c-megaMenu-list {
                display: flex;
                visibility: hidden;
                opacity: 0;
                transform: translateY(-2em);
                z-index: -1;
                transition: all .3s ease-in-out 0s,visibility 0s linear .3s,z-index 0s linear .01s;
                position: absolute;
                &.open-menu {
                    visibility: visible;
                    opacity: 1;
                    z-index: 1;
                    transform: translateY(0);
                    transition-delay: 0s,0s,.3s;
                    position: static;
                }
                >ul {
                    padding: 0 15px;
                    flex: 0 0 25%;
                    max-width: 25%;
                    margin: 30px 0 0;
                    border-right: 1px solid hsla(0,0%,100%,.2);
                    li {
                        padding-bottom: 15px;
                    }
                }
                &.open-menu {
                    visibility: visible;
                    opacity: 1;
                    z-index: 1;
                    transform: translateY(0);
                    transition-delay: 0s,0s,.3s;
                    position: static;
                }
            }
            .megamenu-links {
                height: 100%;
                position: relative;
                ul {
                    position: relative;
                    height: 100%;
                    padding-top: 25px;
                    @media #{$vptabletP} {
                        padding-top: 0;
                    }
                    li {
                        margin-bottom: 20px;
                        position: relative;
                        @media #{$vptabletP} {
                            padding: 20px 40px;
                            margin: 0;
                            &:before {
                                content: "";
                                position: absolute;
                                width: 85%;
                                height: 1px;
                                bottom: 0px;
                                left: 50%;
                                transform: translateX(-50%);
                                background-color: $blue-dark;
                                transition: all 0.35s ease;
                            }
                        }
                        @media #{$vpmobile} {
                            padding: 20px 40px; 
                        }
                        a {
                            color: $black-light;
                            font-size: 20px;
                            font-weight: 700;
                            position: relative;
                            transition: all 0.35s ease;
                            @media #{$vpdesktop} {
                                font-size: 16px;
                            }
                            @media #{$vptabletL} {
                                font-size: 14px;
                            }
                            @media #{$vptabletP} {
                                width: 100%;
                                display: block;
                                font-weight: 500;
                                font-size: 18px;
                                color: #292d83;
                                padding-right: 20px;
                            }
                            @media #{$vpmobile} {
                                font-size: 16px;
                            }
                            .tosvg {
                                width: 13px;
                                height: 13px;
                                margin-left: 10px;
                                margin-right: 0;  
                                top: 50%;
                                position: absolute;
                                transform: translateY(-50%);
                                right: -25px;
                                @media #{$vptabletP} {
                                    right: 0;
                                }
                                path {
                                    stroke: $red;
                                }
                            }
                            &:hover {
                                color: $red;
                            }
                            span {
                                display: block;
                                font-size: 15px;
                                color: #363636;  
                            }
                        }
                        &.mbl-only {
                            display: none;
                            @media #{$vptabletP} {
                                display: block;
                            }
                        }
                        &.subItems {
                            @media #{$vptabletP} {
                                padding-left: 50px;
                            }
                        }
                    }
                    &:after {
                        content: "";
                        position: absolute;
                        background: $blue-dark;
                        width: 1px;
                        height: 100%;
                        top: 0;
                        left: -50px;
                        @media #{$vptabletP} {
                            display: none;
                        }
                    }
                    &.mbl-only {
                        display: none;
                        @media #{$vptabletP} {
                            display: block;
                        }
                    }
                }
                &.multi {
                    display: flex;
                    justify-content: space-between;
                    @media #{$vptabletP} {
                        flex-direction: column;
                    }
                    ul {
                        margin-right: 200px;
                        @media #{$vpdesktop} {
                            margin-right: 80px;
                        }
                        @media #{$vptabletL} {
                            margin-right: 0;
                        }

                        li {
                            a {
                                font-weight: 400;
                            }
                            &:first-child {
                                a {
                                    font-weight: 700;
                                    @media #{$vptabletP} {
                                        font-weight: 500;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
a {
    &.go-back {
        display: none;
        @media #{$vptabletP} {
            width: 100%;
            display: block;
            padding-left:40px;
            color: $gray-light;
            font-size: 16px;
            font-weight: 600;
            line-height: 30px;
            display: flex;
            align-items: center;
            position: absolute;
            top: 30px;
        }
        @media #{$vpssmobile} {
            padding-left: 25px;
        }
        .tosvg {
            transform: rotate(180deg);
            margin-right: 10px;
            path {
                stroke: $blue-dark;
            }
        }
        &:after {
            content: "";
            position: absolute;
            width: 100%;
            top: 40px;
            left: 50%;
            transform: translateX(-50%);
            border-top: 1px solid #b7b7b7;
        }
    }
}
.sec-header .navigation>div {
    padding-left: 14px;
    padding-right: 14px;
    flex-basis: auto;
    flex-grow: 0;
    flex-shrink: 1;
}

body {
    &.js-hiden-body {
        overflow: hidden;
    }
    &.has--breadcrumb {
        .header-breadcrumb {
            display: block;
        }
    }
}

.header-breadcrumb {
    background-color: rgba($blue-dark, 0.7);
    display: none;
    .banking-info {
        .info-wrapper {
            display: flex;
            padding: 10px 0;
            @media #{$vpmobile} {
                justify-content: center;
            }
            @media #{$vpssmobile} {
                flex-direction: column;
                align-items: center;
            }
            .breadcrumb-info {
                display: flex;
            }
            .info-box1 {
                @media #{$vpmobile} {
                    display: none;
                }
                a {
                    color: $white;
                    font-size: 16px;
                    font-weight: 600;
                    line-height: 25px;
                    text-decoration: none;
                    position: relative;
                    @media #{$vptabletP} {
                        font-size: 14px;
                    }
                    @media #{$vpmobile} {
                        font-size: 12px;
                    }
                    @media #{$vpxsmobile} {
                        font-size: 11px;
                    }
                    &::after {
                        content: "";
                        position: absolute;
                        background-image: url(../img/icons/arrow-right.svg);
                        width: 8px;
                        height: 14px;
                        right: -18px;
                        top: 50%;
                        background-repeat: no-repeat;
                        background-size: contain;
                        transform: translateY(-50%);
                    }
                }
            }
            .breadcrumb-links {
                position: relative;
                margin-left: 50px;
                @media #{$vpmobile} {
                    margin-left: 35px;
                }
                @media #{$vpsmmobile} {
                    margin-left: 25px;
                }
                @media #{$vpxsmobile} {
                    font-size: 10px;
                    margin-left: 15px;
                } 
                &:first-child {
                    @media #{$vpmobile} {
                        margin-left: 0;
                    }
                }
                a {
                    color: $white;
                    font-size: 15px;
                    font-weight: 400;
                    line-height: 27px;
                    text-decoration: none;
                    @media #{$vptabletP} {
                        font-size: 14px;
                    }
                    @media #{$vpmobile} {
                        font-size: 12px;
                    }
                    @media #{$vpxsmobile} {
                        font-size: 11px;
                    }
                }
                &::before {
                    content: "";
                    position: absolute;
                    background: $white;
                    width: 1px;
                    height: 27px;
                    left: -27px;
                    top: 0px;
                    @media #{$vptabletP} {
                        left: -20px;
                    }
                    @media #{$vpmobile} {
                        left: -15px;
                    }
                    @media #{$vpxsmobile} {
                        left: -8px;
                        height: 15px;
                        top: 5px;
                    } 
                }
                &:after {
                    content: "";
                    position: absolute;
                    background: $white;
                    width: 0;
                    height: 1px;
                    left: 0;
                    bottom: 0px;
                    transition: all 0.35s ease;
                }
                &:first-child {
                    &::before {
                        display: none;
                    }
                }
                &:hover,
                &.active {
                    &:after {
                        width: 100%;
                    }
                }
            }
        }
    }
}
.mCSB_inside>.mCSB_container {
    margin-right: 15px;
}
.header-shape {
    position: absolute;
    right: 0;
    top: 0;
    &::after {
        content: "";
        height: 11px;
        width: 11px;
        border-color: #e30613 #e30613 transparent transparent;
        border-style: solid;
        border-width: 11px;
        position: absolute;
        right: 0;
        top: 0;
        box-sizing: content-box;
    }
    @media #{$vpssmobile} {
        &:after {
            border-width: 8px;
        }
    }
}