.sec-getin-touch{
    .style-content{
        margin: 0 auto; 
        max-width: 800px;
        text-align: center;
        .item-txt-blue{
            span{
                display: block;
                font-size: 20px;
                line-height: 30px;
                color: $blue-dark;
                font-weight: bold;
                text-transform: uppercase;
                @media #{$vptabletP} {
                    font-size: 15px;
                }
            }
        }
    }
}

.select-box{
    width: 100%;
    max-width: 563px;
    margin: 50px auto;
}
.select2-container--default {
    .select2-selection--single {
        height: 69px;
        border-radius: 0;
        border: 1px solid #707070;
        .select2-selection__rendered {
            color: $black;
            font-size: 20px;
            line-height: 69px;
            padding-left: 19px;
            padding-right: 40px;
            .select2-selection__placeholder{
                color: $black;
                font-size: 20px;
            }
        }
        .select2-selection__arrow{
            height: 69px;
            b{
                position: relative;
                border: 0;
                margin-top: 0;
                margin-left: 0;
                border-radius: 0;
                &:before{
                    content: '';
                    position: absolute;
                    top: 50%;
                    right: 7px;
                    width: 17px;
                    height: 8px;
                    background-size: cover;
                    background-position: 50%;
                    transform: translateY(-50%);
                    background-repeat: no-repeat;
                    background-image: url('../img/icons/arrow-up.svg');
                    transition: all 0.35s ease;
                }
            }
        }
    }
    .select2-dropdown{
        border-radius: 0;
        border: 1px solid #707070;
        border-top: 0;
    }
    &.select2-container--open{
        .select2-selection--single{
            .select2-selection__arrow{
                b{
                    &:before{
                        transform: translateY(-50%) rotate(180deg);
                    }
                }
            }
        }
    }
}

.select2-results{
    .select2-results__options{
        .select2-results__option{
            font-size: 20px;
            padding: 10px 19px;
        }
    }
}

.sec-location--sty{
    overflow-x: hidden;
    .nav{
        &.nav-pills{
            padding: 0;
            justify-content: center;
            background: transparent;
            .nav-item{
                padding: 0px 70px 10px;
                border-bottom: 1px solid #b5b5b5;
                &:first-child{
                    padding-left: 0;
                }
                &:last-child{
                    padding-right: 0;
                }
            }
        }
    }
    .tab-content{
        .tab-pane {
            margin: 0 50px;
            @media #{$vptabletP} {
                margin: 0;
            }
        }
    }
    .sec-find-us {
        position: relative;
        &.bg-grey{
            &:before,
            &:after{
                content: '';
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: -99%;
                background: $light-grey;
            }
            &:after{
                left: auto;
                right: -99%;
            }
        }
    }
}
.contact--mobile {
    margin-top: 0 !important;
}