.tab-section {
    .h2 {
        margin-bottom: 50px;
        text-align: center;
        @media #{$vptabletP}  {
            margin-bottom: 25px;
        }
    }
    .content-tabs {
        .sec-tab {
            background: $blue-dark;
            padding: 20px 0 65px 35px;
            @media #{$vpdesktop} {
                padding: 20px 0 30px 15px;
            }
            .nav-link {
                color: $white;
                font-weight: 600;
                padding-right: 70px;
                position: relative;
                margin: 15px 0;
                display: flex;
                align-items: center;
                padding-top: 0;
                padding-bottom: 0;
                min-height: 72px;
                @media #{$vpMddesktop} {
                    font-size: 18px;
                }
                @media #{$vptabletL} {
                    font-size: 16px;
                    padding-right: 50px;
                }
                @media #{$vpdesktop} {
                    margin: 10px 0;
                    min-height: 61px;
                }
                &:after {
                    content: "";
                    position: absolute;
                    background: url('../img/icons/arrow-right.svg');
                    background-repeat: no-repeat;
                    width: 30px;
                    height: 30px;
                    right: 30px;
                    top: 50%;
                    transform: translateY(-50%);
                    @media #{$vpdesktop} {
                        height: 25px;
                    }
                    @media #{$vptabletL} {
                        right: 10px;
                    }
                }
                &:before {
                    content: "";
                    position: absolute;
                    border-bottom: 1px solid $white; 
                    width: 90%;
                    height: 1px;
                    bottom: -10px;
                    left: 0;
                }
                &.active {
                    background: $gray-light2;
                    color: $blue-dark;
                    height: 95px;
                    border-radius: 0;
                    &:after {
                        background: url(../img/icons/arrow-right-blue.svg);
                        background-repeat: no-repeat;
                    }
                    &:before {
                        opacity: 0;
                    }
                }
            }
        }
        .tab-detail {
            display: flex;
            align-items: center;
            background: $gray-light2;
            padding: 30px 30px 30px 78px; 
            margin: 15px 0;
            height: 95%;
            min-height: 480px;
            @media #{$vpdesktop} {
                height: 80%;
                padding: 20px 30px; 
                min-height: 380px;
            }
            .post-box {
                display: flex;
                .sec-content {
                    display: flex;
                    flex: 0 0 50%;
                    max-width: 50%;
                    padding-right: 30px;
                    flex-direction: column;
                    justify-content: center;
                    a {
                        @media #{$vptabletP} {
                            color: $blue-dark;
                        }
                        &.corporate {
                            color: $blue-dark;
                            font-size: 16px;
                            font-weight: 700;
                            margin-bottom: 5px;
                            text-transform: uppercase;
                            @media #{$vpdesktop} {
                                font-size: 13px;
                            }
                            @media #{$vptabletP} {
                                font-size: 11px;
                            }
                            cursor: pointer;
                            &:hover {
                                color: $red;
                            }
                        }
                    }
                }
                .sec-img {
                    flex: 0 0 50%;
                    max-width: 50%;
                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        transition: all 0.35s ease;
                    }
                    &:hover {
                        img {
                            transform: scale(1.05);
                        }
                    }
                }
            }
        }
    }
    .tabs-mbl {
        display: none;
        @media #{$vptabletP} {
            display: block;
            margin-top: 30px;
            .card {
                border: none;
                .card-header {
                    background-color: $blue-dark;
                    border-radius: 0;
                    border-color: $white;
                    .tabs-title {
                        border: none;
                        background: transparent;
                        width: 100%;
                        text-align: left;
                        font-size: 16px;
                        color: $white;
                        position: relative;
                        &:focus {
                            outline: none;
                        } 
                        &:after {
                            content: "";
                            position: absolute;
                            background-image: url(../img/icons/arrow-up-white.svg);
                            background-repeat: no-repeat;
                            width: 20px;
                            height: 10px;
                            top: 50%;
                            transform: translateY(-50%);
                            right: 20px;
                            transition: all 0.35s ease;
                        }
                        &.collapsed {
                            &:after {
                                transform: translateY(-50%) rotate(180deg);
                            }
                        }
                        @media #{$vpxsmobile} {
                            padding-right: 50px;
                        }
                    } 
                }
            }
        }
        .bar-detail {
            padding: 50px 0;
            @media #{$vpmobile} {
                padding: 20px 0;
            }
            &:first-child {
                padding-top: 0;
            }
            &:last-child {
                padding-bottom: 0;
            }
            .detail-box {
                .card {
                    border: none;
                    .card-header {
                        background: transparent;
                        padding: 15px 8px;
                        border-color: $black-light;
                        .btn-link {
                            width: 100%;
                            max-width: 100%;
                            border: none;
                            font-size: 22px;
                            color: $red;
                            transition: all 0.35s ease;
                            text-decoration: none;
                            position: relative;
                            padding-right: 50px;
                            transform: translateX(0);
                            @media #{$vpmobile} {
                                font-size: 14px;
                            }
                            &:after {
                                content: "";
                                position: absolute;
                                background: url(../img/icons/ic-subtract.svg);
                                background-repeat: no-repeat;
                                width: 36px;
                                height: 36px;
                                top: 50%;
                                transform: translateY(-50%);
                                right: 10px;
                                transform: all 0.35s ease;
                            }
                            &.collapsed {
                                color: $black-light;
                                &:after {
                                    content: "";
                                    position: absolute;
                                    background: url(../img/icons/ic-plus.svg);
                                    background-repeat: no-repeat;
                                    width: 36px;
                                    height: 36px;
                                    top: 50%;
                                    transform: translateY(-50%);
                                    right: 10px;
                                    transform: all 0.35s ease;
            
                                }
                            }
                            &:hover {
                                text-decoration: none;
                                color: $red;
                            }
                        }
                    }
                    .card-body {
                        padding-left: 8px;
                    }
                }
            }
            .get-in-touch-detail {
                display: flex;
                margin-top: 30px;
                @media #{$vpmobile} {
                    flex-direction: column;
                    align-items: center;
                }
                p {
                    margin-right: 50px;
                    @media #{$vpmobile} {
                        margin-right: 0;
                    }
                }
            }
        }
        .post-box {
            .sec-content {
               .corporate {
                    color: #292d83;
                    font-size: 16px;
                    font-weight: 700;
                    margin-bottom: 5px;
                    text-transform: uppercase;
                    cursor: pointer;
                    @media #{$vpmobile} {
                        font-size: 12px;
                    } 
               }
               h3 {
                   br {
                        @media #{$vpmobile} {
                            display: none;
                        }
                   }
                        
               }
            }
            .sec-img {
                margin-top: 30px;
                text-align: center; 
            }
        }
    }
}