.sec-signup {
    background: $gray-light2;
    padding: 55px 0;
    @media #{$vptabletP} {
        padding: 40px 0;
    }
    .signup-box {
        padding: 0 100px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width: 1650px) {
            padding: 0;
        }
        @media #{$vptabletL}  {
            flex-direction: column;
        }
        
        .h2 {
           
            margin-bottom: 24px;
        }
        .post-content {
            flex: 0 0 37%;
            max-width: 37%;
            @media #{$vptabletL}  {
                flex: 0 0 100%;
                max-width: 100%;
            }
            @media #{$vptabletP} {
                text-align: center;
                padding: 0 30px;
            }
        }
        .subscribebox {
            position: relative;
            padding: 0;
            flex: 0 0 55%;
            max-width: 55%;
            background: transparent;
            @media #{$vptabletL}  {
                width: 100%;  
                flex: 0 0 100%;
                max-width: 100%;
                margin-top: 30px;
            }
            input.subscrib {
                outline: none;
                background: transparent;
                border: none;
                width: 70%;
                padding: 14px 50px 5px 14px;
                border-bottom: 2px solid #363636;
                border-radius: 0;
                @media (max-width: 1650px) {
                    width: 65%;
                }
                @media #{$vptabletL}  {
                    width: 75%;
                }
                &::placeholder {
                    color: #ACACAC;
                }
                @media #{$vptabletP}  {
                    width: 80%;
                    margin: 0 auto;
                    padding: 14px 14px 5px 14px;;
                }
                &:focus {
                    outline: none;
                    box-shadow: none;
                }
            }
            button {
                text-transform: uppercase;
                width: auto;
                float: none;
                border: 1px solid $blue-dark;
                position: absolute;
                top: 0;
                right: 0;
                background: $blue-dark;
                color: $white;
                font-size: 18px;
                padding: 11px 43px;
                font-weight: 600;
                transition: all 0.35s ease;
                &:focus {
                    outline: none;
                }
                &:hover {
                    background: transparent;
                    color: $blue-dark;
                }
                @media #{$vptabletP}  {
                    position: relative;
                    width: 220px;
                    margin: 30px auto 0 auto;
                }
            }
            form {
                @media #{$vptabletP}  {
                    display: flex;
                    flex-direction: column;
                }
            }
            .error {
                display: none;
                p {
                    color: $red;
                }
            }
            .success {
                display: none;
                p {
                    color: $blue-dark;
                }
            }
            .form-group {
                .required-msg {
                    position: absolute;
                    .error-email {
                        font-size: 14px;
                        color: $red;
                    }
                }
                &.invalid-field {
                    .required-msg {
                        display: block;
                    }   
                }
            }
        }
    }
}
.footer {
    position: relative;
    .sec-footer {
        background: $blue-dark;
        padding: 65px 0 75px;
        .quicklinks {
            ul {
                li {
                   &.head {
                    color: $white;
                    font-weight: 600;
                    margin-bottom: 20px;
                    @media #{$vpssmobile}  {
                        margin-top: 20px;
                    }
                   }
                    a {
                        color: $white;
                        font-size: 16px;
                        position: relative;
                        line-height: 2;
                        @media #{$vpdesktop} {
                            font-size: 14px;
                        }
                        @media #{$vptabletP} {
                            font-size: 12px;
                        }
                        &:after {
                            content: "";
                            position: absolute;
                            width: 0;
                            left: 0;
                            bottom: -5px;
                            height: 1px;
                            border-bottom: 1px solid $white;
                            transition: all 0.35s ease;
                        }
                        &:hover {
                            &:after {
                                width: 100%;
                            }
                        }
                    }
                }
            }
        }
        .social-tabs {
            text-align: right;
            @media #{$vptabletP}  {
                text-align: center;
                margin-top: 40px;
            }
            .social-links {
                margin-top: 40px;
                ul {
                    display: flex;
                    justify-content: flex-end;
                    @media #{$vptabletP}  {
                        text-align: center;
                        justify-content: center;
                    }
                    li {
                       padding: 0 17px;
                       &:first-child {
                           padding-left: 0;
                       } 
                       &:last-child {
                         padding-right: 0;
                        }
                    }
                }
            }
        }
    }
    .foot {
        .sec-foot {
            display: flex;
            justify-content: space-between;
            padding: 10px 0;
            align-items: center;
            @media #{$vptabletP}  {
                flex-wrap: wrap;
                flex-direction: column-reverse;
            }
            .copyright {
                p {
                    font-size: 14px;
                    color: $blue-dark;
                }
                @media #{$vptabletP}  {
                    max-width: 100%;
                    flex: 0 0 100%;
                    text-align: center;
                }
            }
            .foot-tabs {
                ul {
                    display: flex;
                    @media #{$vptabletP}  {
                        max-width: 100%;
                        flex: 0 0 100%;
                        justify-content: center;
                    }
                    li {
                        padding: 0 22px;
                        position: relative;
                        @media #{$vpmobile}  {
                            padding: 10px;
                        }
                        a {
                           font-size: 16px; 
                           color: $blue-dark;
                           position: relative;
                           @media #{$vpdesktop} {
                            font-size: 14px;
                        }
                            @media #{$vptabletP} {
                                font-size: 12px;
                            }
                           &:after {
                               content: "";
                               position: absolute;
                               width: 0;
                               height: 1px;
                               border-bottom: 1px solid $blue-dark;
                               left: 0;
                               bottom: 0;
                               transition: all 0.35s ease;
                           }
                           &:hover {
                               &:after {
                                   width: 100%;
                               }
                           }
                        }
                        &:last-child {
                            padding-right: 0;
                        }
                        &:first-child {
                            padding-left: 0;
                        }
                        &:after {
                            content: "";
                            position: absolute;
                            width: 1px;
                            height: 100%;
                            border-left: 1px solid $blue-dark;
                            top: 50%;
                            transform: translateY(-50%);
                            right: 0;
                            @media #{$vpmobile}  {
                                display: none;
                            }
                        }
                        &:last-child {
                            &:after {
                                display: none;
                            }
                        }
                    }
                    @media #{$vpmobile}  {
                        flex-wrap: wrap;
                    }
                }
            }
        }
    }
}

.footer-shape {
    position: absolute;
    right: 0;
    top: 0;
    &::after {
        content: "";
        height: 23px;
        width: 23px;
        border-color: #e30613 #e30613 transparent transparent;
        border-style: solid;
        border-width: 23px;
        position: absolute;
        right: 0;
        top: 0;
        box-sizing: content-box;
    }
}