.sec-about-agb {
    .about-agb-wrapper {
        .about-agb-content {
            text-align: center;
            max-width: 850px;
            margin: 0 auto;
            @media #{$vpmobile}  {
                width: 100%;
            }
            .item-txt-blue {
                span {
                    color: $blue-dark;
                    font-weight: bold;
                    line-height: 30px;
                    text-transform: uppercase;
                }
            }
            .style-content {
                margin-bottom: 45px;
                @media #{$vptabletP}  {
                    margin-bottom: 25px;
                }
                h2 {
                    margin: 13px 0 7px 0;
                }
            }
        }
        .about-agb-img {
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        .open-account-btn {
            text-align: center;
            margin-top: 50px;
            @media #{$vptabletP}  {
                margin-top: 25px;
            }
        }
    }
}
.sec-agb-details {
    background-color: $light-grey;
    .agb-details-wrapper {
        max-width: 1200px;
        text-align: center;
        margin: 0 auto;
        @media #{$vpmobile}  {
            max-width: 100%;
        }
        .agb-details-content {
            .h2 {
                padding-bottom: 20px;
                margin: 0;
            }
            p {
                color: $black-light;
                font-weight: 400;
                margin: 20px 0 0 0;
            }
        }
    }
}
.sec-cards {
    .account-head {
        max-width: 825px;
        margin: 0 auto;
        h2 {
            text-align: center;
        }
        p {
            text-align: center;
        }
    }
    .sec-cards-wrapper {
        display: flex;
        justify-content: space-between;
        margin-top: 45px;
        @media #{$vptabletP}  {
            flex-wrap: wrap;
            margin-top: 25px;
        }
        .about-agb-card {
            max-width: 32%;
            flex: 0 0 32%;
            background-color: $light-grey;
            @media #{$vptabletP}  {
                max-width: 48%;
                flex: 0 0 48%;
                margin: 0 auto 30px;
            }
            @media #{$vpmobile}  {
                max-width: 100%;
                flex: 0 0 100%;
                margin-bottom: 14px;
            }
            .card-img {
                overflow: hidden;
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    transition: all 0.35s ease;
                }
                &:hover {
                    img {
                        transform: scale(1.05);
                    }
                }
            }
            .card-content {
                padding: 30px;
                @media #{$vptabletP}  {
                    padding: 15px 30px;
                }
                .h3 {
                    margin-bottom: 20px;
                    text-align: left;
                    @media #{$vpdesktop}  {
                        margin-bottom: 10px;
                    }
                }
                p {
                    min-height: 135px;
                    margin-bottom: 35px;
                    @media #{$vpdesktop}  {
                        min-height: 130px;
                    }
                    @media #{$vptabletP}  {
                        min-height: 50px;
                    }
                    @media #{$vpmobile}  {
                        min-height: 0;
                    }
                    &.content-para {
                        min-height: 0;
                    }
                }
            }
        }
    }
}
.sec-content-box {
    background-color: $light-grey;
    padding: 95px 0;
    @media #{$vptabletP}  {
        padding: 45px 0;
    }
    .content-box {
        background-color: $white;
        .content-box-wrapper {
            display: flex;
            @media #{$vptabletP}  {
                flex-wrap: wrap;
            }
            .content-box-content {
                max-width: 60%;
                flex: 0 0 60%;
                padding: 45px 60px;
                @media #{$vptabletP}  {
                    max-width: 100%;
                    flex: 0 0 100%;
                }
                p {
                    color: $black-light;
                    font-weight: 400;
                    padding: 10px 0 20px 0;
                }
            }
            .content-box-img {
                max-width: 40%;
                flex: 0 0 40%;
                @media #{$vptabletP}  {
                    max-width: 100%;
                    flex: 0 0 100%;
                }
                img {
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
}
.sec-saving-accounts {
    .section-heading {
        max-width: 825px;
        margin: 0 auto;
        .h2 {
            margin-bottom: 10px;
            text-align: center;
        }
        p {
            padding: 10px 0 20px 0;
            text-align: center;
            margin-bottom: 30px;
            @media #{$vptabletP}  {
                margin-bottom: 10px;
            }
        }
    }
    .sec-wrapper {
        display: flex;
        max-width: 1200px;
        margin: 0 auto;
        justify-content: space-between;
        @media #{$vpmobile}  {
            flex-wrap: wrap;
            flex-direction: column;
        }
        .card-grey {
           &.bg-blue {
            background-color: $blue-dark;
           }
            .card-grey-img {
                overflow: hidden;
                img {
                    width: 100%;
                    transition: all 0.35s ease;
                }
                &:hover {
                    img {
                        transform: scale(1.05);
                    }
                }
            }
            background-color: $gray-light2;
            max-width: 47%;
            flex: 0 0 47%;
            @media #{$vpmobile}  {
                max-width: 100%;
                margin-bottom: 35px;
            }
            @media #{$vptabletP}  {
                margin-bottom: 25px;
            }
            .card-grey-content {
                padding: 30px 40px;
                transition: all 0.35s ease;
                @media #{$vptabletP}  {
                    padding: 15px 30px;
                }
            }
        }
    }
}
.current-account-btn {
    text-align: center;
    margin-top: 50px;
    @media #{$vptabletP} {
        margin-top: 50px;
    }
    @media #{$vpmobile}  {
        margin-top: 30px;
    }
    @media #{$vpmobile}  {
        margin-top: 30px;
    }
}
.sec-find-us {
    .find-us-wrapper{
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media #{$vptabletP}  {
            flex-wrap: wrap;
            flex-direction: column;
            align-items: baseline;
        }
        .branch-content {
            max-width: 55%;
            flex: 0 0 55%;
            padding: 20px 0;
            @media #{$vptabletP}  {
                max-width: 100%;
                order: 2;
            }
            .branch-heading {
                .h3 {
                    margin-bottom: 30px;
            }
            }
            .adress {
                max-width: 600px;
                margin-bottom: 30px;
                span {
                    color: $black-light;
                    font-size: 20px;
                    font-weight: 700;
                    line-height: 27px;
                }
            }
            .services-info {
                display: flex;
                justify-content: space-between;
                @media #{$vpmobile}  {
                    flex-wrap: wrap;
                    flex-direction: column;
                }
                .services {
                    max-width: 50%;
                    flex: 0 0 50%;
                    @media #{$vptabletP}  {
                        padding-right: 30px;
                    }
                    @media #{$vpmobile}  {
                        max-width: 100%;
                        padding: 0;
                    }
                    span {
                        color: $black-light;
                        font-size: 20px;
                        font-weight: 700;
                        line-height: 27px;
                    }
                }
                .contact-info {
                    max-width: 40%;
                    flex: 0 0 40%;
                    @media #{$vptabletP}  {
                        max-width: 100%;
                        flex: 0 0 100%;
                    }
                    @media #{$vpmobile}  {
                        max-width: 100%;
                    }
                    .contact {
                        margin-bottom: 30px;
                        span {
                            color: $black-light;
                            font-size: 20px;
                            font-weight: 700;
                            line-height: 27px;
                        }
                    }
                    .hours {
                        span {
                            color: $black-light;
                            font-size: 20px;
                            font-weight: 700;
                            line-height: 27px;
                        }
                        p{
                            margin: 0;
                        }
                    }
                }
            }
            .location-btn {
                margin-top: 35px;
            }
        }
        .location-map {
            max-width: 40%;
            flex: 0 0 40%;
            @media #{$vptabletP}  {
                max-width: 100%;
                order: 1;
                width: 100%;
            }
            img {
                width: 100%;
            }
            .map-style {
                position: relative;
                height: 400px;
                #map,
                #map2,
                #map3,
                #map4 {
                    position: relative;
                    overflow: hidden;
                    width: 100%;
                    height: 100%;  
                }
            }
        }
    }
    &.bg-grey {
        background-color: $light-grey;
        padding: 60px 0;
    }
}
.mt-15 {
    margin-top: 15px !important;
}