.sec-services {
    .trade--desktop,
    .contact--desktop {
        display: block;
        @media #{$vptabletP} {
            display: none;
        }
    }
    .trade--mobile,
    .contact--mobile
     {
        display: none;
        @media #{$vptabletP} {
            display: block;
            margin-top: 30px;
            .card {
                border: none;
                .card-header {
                    background-color: $blue-dark;
                    border-radius: 0;
                    border-color: $white;
                    .service-title {
                        border: none;
                        background: transparent;
                        width: 100%;
                        text-align: left;
                        font-size: 16px;
                        color: $white;
                        position: relative;
                        padding-right: 40px;
                        &:focus {
                            outline: none;
                        } 
                        &:after {
                            content: "";
                            position: absolute;
                            background-image: url(../img/icons/arrow-up-white.svg);
                            background-repeat: no-repeat;
                            width: 20px;
                            height: 10px;
                            top: 50%;
                            transform: translateY(-50%);
                            right: 20px;
                            transition: all 0.35s ease;
                        }
                        &.collapsed {
                            &:after {
                                transform: translateY(-50%) rotate(180deg);
                            }
                        }
                    } 
                }
            }
        }
        .bar-detail {
            padding: 50px 0;
            @media #{$vpmobile} {
                padding: 20px 0;
            }
            &:first-child {
                padding-top: 0;
            }
            &:last-child {
                padding-bottom: 0;
            }
            .detail-box {
                .card {
                    border: none;
                    .card-header {
                        background: transparent;
                        padding: 15px 8px;
                        border-color: $black-light;
                        .btn-link {
                            width: 100%;
                            max-width: 100%;
                            border: none;
                            font-size: 22px;
                            color: $red;
                            transition: all 0.35s ease;
                            text-decoration: none;
                            position: relative;
                            padding-right: 50px;
                            transform: translateX(0);
                            @media #{$vpmobile} {
                                font-size: 14px;
                            }
                            &:after {
                                content: "";
                                position: absolute;
                                background: url(../img/icons/ic-subtract.svg);
                                background-repeat: no-repeat;
                                width: 36px;
                                height: 36px;
                                top: 50%;
                                transform: translateY(-50%);
                                right: 10px;
                                transform: all 0.35s ease;
                            }
                            &.collapsed {
                                color: $black-light;
                                &:after {
                                    content: "";
                                    position: absolute;
                                    background: url(../img/icons/ic-plus.svg);
                                    background-repeat: no-repeat;
                                    width: 36px;
                                    height: 36px;
                                    top: 50%;
                                    transform: translateY(-50%);
                                    right: 10px;
                                    transform: all 0.35s ease;
            
                                }
                            }
                            &:hover {
                                text-decoration: none;
                                color: $red;
                            }
                        }
                    }
                    .card-body {
                        padding-left: 8px;
                    }
                }
            }
            .get-in-touch-detail {
                display: flex;
                margin-top: 30px;
                @media #{$vpmobile} {
                    flex-direction: column;
                    align-items: center;
                }
                p {
                    margin-right: 50px;
                    @media #{$vpmobile} {
                        margin-right: 0;
                    }
                }
            }
        }
    }
    ul {
        display: flex;
        justify-content: space-around;
        padding: 10px 50px;
        background: $light-grey;
        @media #{$vpMddesktop} {
            padding:10px 20px;
        }
        &.tab-small {
            background: transparent;
            justify-content: space-around;
            li {
                a {
                    font-size: 15px;
                    @media #{$vptabletL} {
                        font-size: 13px;
                    }
                    &:after {
                        height: 1px;
                        border-bottom: 3px solid $blue-dark;
                    }
                }
                &:after {
                    display: none;
                }
            }

        }
        @media #{$vptabletL} {
            padding: 10px 15px;
        }
        li {
            position: relative;
            a {
                color: #48545D;
                font-weight: 600;
                position: relative;
                padding: 0;
                &:after {
                    content: "";
                    position: absolute;
                    width: 0;
                    height: 2px;
                    border-bottom: 5px solid $red;
                    bottom: -10px;
                    left: 0;
                    transition: all 0.35s ease;
                }
                &.active,
                &:hover {
                    background-color: transparent !important;
                    color: $black-light !important;
                    &:after {
                        width: 100%;
                    }
                }
                &.para-lg {
                    @media #{$vptabletL} {
                        font-size: 15px;
                    }
                }
            }
        }
    }
    .service-detail {
        padding: 50px 65px 45px;
        @media #{$vptabletP} {
            padding: 0;
        }
        .sec-head {
            text-align: center;
        }
        .post-services {
            margin-top: 35px;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            .post-detail {
                display: flex;
                flex: 0 0 45%;
                max-width: 45%;
                padding: 27px 0;
                @media #{$vptabletP} {
                   display: block;
                }
                @media #{$vpssmobile} {
                    flex: 0 0 100%;
                    max-width: 100%;
                    padding: 20px 0;
                 }
                .post-image {
                    display: flex;
                    flex: 0 0 30%;
                    max-width: 30%;
                    overflow: hidden;
                    img {
                        transition: all 0.35s ease;
                        object-fit: cover;
                        width: 100%;
                        height: 100%;
                    }
                    &:hover {
                        img {
                            transform: scale(1.05);
                        }
                    }
                    @media #{$vptabletP} {
                        flex: 0 0 100%;
                        max-width: 100%;
                    }
                }
                .post-description {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    flex: 0 0 60%;
                    max-width: 60%;
                    margin-left: 27px;
                    @media #{$vptabletP} {
                        flex: 0 0 100%;
                        max-width: 100%;
                        margin-left: 0;
                    }
                    h3 {
                        font-size: 24px;
                        font-weight: 600;
                        @media #{$vptabletL} {
                            font-size: 20px;
                        }
                        @media #{$vptabletP} {
                            font-size: 18px;
                            min-height: 45px;
                        }
                        @media #{$vpssmobile} {
                            min-height: 100%;
                        }
                    }
                    p {
                        margin: 10px 0 20px;
                    }
                }
            }
            @media #{$vpssmobile} {
                margin: 0;
            }
        }
        &.legal {
            .post-services {
                .post-detail {
                    .post-description {
                        flex: 0 0 100%;
                        max-width: 100%;
                        h3 {
                            @media #{$vptabletP} {
                                min-height: auto;
                            }
                        }
                    }
                }
            }
        }
    }
    &.side-bar {
        .c-categoryBox {
            ul {
                background: transparent;
                flex-direction: column;
                li {
                    margin-bottom: 30px;
                    position: relative;
                    padding-right: 100px;
                    &:last-child {
                        margin-bottom: 0;
                    }
                    @media #{$vptabletL} {
                        padding-right: 50px;
                    }
                    a {
                        font-size: 18px;
                        &:after {
                            border: none;
                        }
                        &:hover {
                            color: $red !important;
                            &:after {
                                border: none;
                            }
                        }
                    }
                    &.active {
                        a {
                            color: $red;
                        }
                        &:after {
                            content: "";
                            position: absolute;
                            background: url(../img/icons/arrow-red.svg);
                            background-repeat: no-repeat;
                            width: 36px;
                            height: 22px;
                            top: 50%;
                            transform: translateY(-50%);
                            right: 50px;
                            transform: all 0.35s ease;
                            display: block;
                            @media #{$vpMddesktop} {
                                background-size: 60%;
                                right: 40px;
                            }
                        }
                    }
                    &:after {
                        display: none;
                    }

                }
            }
            @media #{$vptabletP} {
                display: none;
            }
        }
        .sec-content {
            padding: 100px 0;
            @media #{$vptabletP} {
                padding: 30px 0;
            }
            .bar-detail {
                padding: 50px 0;
                @media #{$vptabletP} {
                    padding: 30px 0 0 0;
                }
                &:first-child {
                    padding-top: 0;
                }
                &:last-child {
                    padding-bottom: 0;
                }
                .detail-box {
                    .card {
                        border: none;
                        .card-header {
                            background: transparent;
                            padding: 15px 8px;
                            border-color: $black-light;
                            .btn-link {
                                width: 100%;
                                max-width: 100%;
                                border: none;
                                font-size: 22px;
                                color: $red;
                                transition: all 0.35s ease;
                                text-decoration: none;
                                position: relative;
                                padding-right: 50px;
                                transform: translateX(0);
                                @media #{$vptabletL} {
                                    font-size: 18px;
                                }
                                &:after {
                                    content: "";
                                    position: absolute;
                                    background: url(../img/icons/ic-subtract.svg);
                                    background-repeat: no-repeat;
                                    width: 36px;
                                    height: 36px;
                                    top: 50%;
                                    transform: translateY(-50%);
                                    right: 10px;
                                    transform: all 0.35s ease;
                                    @media #{$vpmobile} {
                                        background-position: center;
                                        background-size: 25px;
                                    }
                                }
                                &.collapsed {
                                    color: $black-light;
                                    &:after {
                                        content: "";
                                        position: absolute;
                                        background: url(../img/icons/ic-plus.svg);
                                        background-repeat: no-repeat;
                                        width: 36px;
                                        height: 36px;
                                        top: 50%;
                                        transform: translateY(-50%);
                                        right: 10px;
                                        transform: all 0.35s ease;
                                        @media #{$vpmobile} {
                                            background-position: center;
                                            background-size: 25px;
                                        }
                
                                    }
                                }
                                &:hover {
                                    text-decoration: none;
                                    color: $red;
                                }
                                @media #{$vptabletP} {
                                    font-size: 14px;
                                }
                            }
                        }
                        .card-body {
                            padding-left: 8px;
                        }
                    }
                }
                .get-in-touch-detail {
                    display: flex;
                    margin-top: 30px;
                    p {
                        margin-right: 50px;
                        @media #{$vpmobile} {
                            margin: 0 0 10px 0;
                        }
                    }
                    @media #{$vpmobile} {
                        flex-direction: column;
                        align-items: center;
                    }
                }
            }
        }
        .page-tabs {
            @media #{$vptabletP} {
                overflow-x: scroll;
                flex-wrap: nowrap;
                white-space: nowrap;
                justify-content: flex-start;
                    li {
                        padding: 10px 15px; 
                        &:first-child {
                            padding-left: 0;
                        }
                        a {
                            display: inline-block;
                            width: max-content;
                        }
                    }
                }
            }
        }
    }   

.is--fixed {
    position: fixed;
    top: 123px;
}

.is--bottom {
    position: absolute;
    bottom: 0;
}
.tab-content {
    &.services {
        .tab-pane {
            &.collapse {
                display: none;
            }
            &.show {
                display: block;
            }
        }
        .card-header {
            background-color: $white;
            .service-title {
                border: 0;
                background-color: transparent;
                position: relative;
                width: 100%;
                text-align: left;
                &:focus {
                    outline: 0;
                }
                &:after {
                    content: "";
                    position: absolute;
                    background-image: url(../img/icons/arrow-up-black.svg);
                    top: 9px;
                    width: 16px;
                    right: 0;
                    height: 10px;
                    background-repeat: no-repeat;
                    transform: rotate(0);
                    transition: all 0.35s ease;
                }
                &.collapsed {
                    &:after {
                        transform: rotate(180deg);
                    }
                }
                @media #{$vpmobile} {
                    font-size: 16px;
                }
                @media #{$vpssmobile} {
                      padding-right: 25px;
                }
            }
            @media #{$vptabletP} {
                padding-left: 0;
            }
        }
        .service-detail {
            @media #{$vptabletP} {
                margin-top: 30px;
            }
        }
        @media #{$vptabletP} {
            margin-top: 10px;
        }
    }
}